import React from 'react';

import './style.scss';

interface ContenLoaderProps {
    isActive: boolean;
    className?: string;
    onLoading: React.ReactNode;
}

const ContainerLoader: React.FC<ContenLoaderProps> = ({isActive = false, onLoading, children, className = ''}) => {
    const [boundingClientRect, setBoundingClientRect] = React.useState({width: 0, height: 0});
    const containerRef: React.Ref<any> = React.useRef(null);

    React.useEffect(() => {
        setBoundingClientRect(containerRef.current
            ? containerRef.current.getBoundingClientRect()
            : {
                width: 0,
                height: 0,
            });
    }, [containerRef, isActive]);

    return (
        <React.Fragment>
            <div
                ref={containerRef}
                className={`ContainerLoader__parentNode ${isActive ? 'ContainerLoader__parentNode_transparent' : ''} ${className}`}
                onClick={(e) => isActive && e.preventDefault()}
            >
                {children}
            </div>
            {isActive && containerRef.current && (
                <div className="ContainerLoader">
                    {onLoading}
                </div>
            )}
        </React.Fragment>
    );
};


export default ContainerLoader;
