import React from 'react';
import {
    withFocusable,
    FocusableProps,
} from '@noriginmedia/react-spatial-navigation';

import Card from 'components/Card';
import Typography from 'components/Typography';

import withBem from 'hoc/withBem';
import {ProductItemModel} from 'stores/product';

import './style.scss';

interface ProductProps {
    className?: string;
    bem?: BemRenderer;
    countInCart: number;
    itemId: string;
}

const DEFAULT_ALT = 'Изображение';

const Product: React.FC<ProductProps & FocusableProps & ProductItemModel> = withBem(({
    focused,
    bem,
    imagesUrls,
    clearName,
    countInCart,
    name,
    price,
    className = '',
    index,
    id,
}) => {
    return (
        <div className={bem.all(className, bem.modify({focused}))} data-itemId={id}>
            <Card index={index} className={bem('card')}>
                <img className={bem('img')} src={imagesUrls[0]} alt={clearName || DEFAULT_ALT} />
            </Card>
            <div className={bem('description')}>
                <span className={bem.modify({focused}, 'callToAction')} />
                <div className={bem('row')}>
                    <span className={bem('title', 'name')}>{name}</span>
                </div>
                <div className={bem('priceRow')}>
                    <span className={bem('title', 'price')}>{price} ₽</span>
                    {countInCart && <span className={bem('title', 'badge')}>{countInCart}</span>}
                </div>
            </div>
        </div>

    );
}, 'Product');

export default withFocusable()(Product);
