import React from 'react';
import {
    withFocusable,
    FocusableProps,
} from '@noriginmedia/react-spatial-navigation';

import Container from 'components/Grids/Container';
import Row from 'components/Grids/Row';

import Skeleton from 'components/Skeleton';
import ContentLoader from 'components/ContentLoader';
import Navigation from 'containers/Navigation';
import ProductsList from 'containers/ProductsList';
import Cart from 'containers/CartNavigation';
import Address from 'containers/Address';

import {connectToStore} from 'tools/connect';
import {routes} from 'stores/ui';

import {StoreType} from 'stores/types';

const MainPage: React.FC<StoreType & FocusableProps> = ({ui, cart, setFocus}) => {
    React.useEffect(() => {
        setFocus('ITEMS');
    }, [setFocus, ui.isLoading]);

    const hasItemsInCart = cart.count > 0;

    const NavigationRightElement = (
        <>
            <Address onEnterPress={(): void => ui.gotoPage(routes.MAP)} isShowAddress={!hasItemsInCart} />
            {hasItemsInCart && <Cart onEnterPress={(): void => ui.gotoPage(routes.CART)} />}
        </>
    );

    const onLoading = (
        <Container>
            <Row>
                <Skeleton backgroundColor="#262828" foregroundColor="#414545" />
            </Row>
        </Container>
    );

    return (
        <>
            <Navigation title="Самокат" subtitle="«Хочу еду к просмотру»" rightElement={NavigationRightElement} />
            <ContentLoader isActive={ui.isLoading} onLoading={onLoading}>
                <ProductsList trackChildren focusKey="ITEMS" />
            </ContentLoader>
        </>
    );
};

export default withFocusable({
    trackChildren: true,
})(connectToStore(MainPage));
