import React from 'react';
import ContentLoader from 'react-content-loader';

interface SkeletonProps {
    width?: number;
    heading?: {
        width: number;
        height: number;
    };
    row?: number;
    column?: number;
    padding?: number;
    borderRadius?: number;
    [key: string]: any;
}

const Skeleton: React.FC<SkeletonProps> = ({
    width = 960,
    row = 1,
    column = 4,
    padding = 8,
    borderRadius = 8,
    ...props
}) => {
    const list = [];
    let height;

    for (let i = 1; i <= row; i += 1) {
        for (let j = 0; j < column; j += 1) {
            const itemWidth = (width - padding * (column + 1)) / column;
            const x = j * (itemWidth + padding * 2);
            const height1 = 221;
            const height2 = 20;
            const height3 = 20;
            const y1 = 0;
            const y2 = y1 + padding + height1;
            const y3 = y2 + padding / 2 + height2;

            list.push(
                <>
                    <rect
                        key={`${i}${j}1`}
                        x={x}
                        y={y1}
                        rx={borderRadius}
                        ry={borderRadius}
                        width={itemWidth}
                        height={height1}
                    />
                    {/* <rect key={`${i}${j}2`} x={x} y={y2} rx={borderRadius} ry={borderRadius} width={itemWidth} height={height2} />
                    <rect
                        key={`${i}${j}3`}
                        x={x}
                        y={y3}
                        rx={borderRadius}
                        ry={borderRadius}
                        width={itemWidth * 0.6}
                        height={height3}
                    /> */}
                </>,
            );

            if (i === row) {
                height = y3 + height3;
            }
        }
    }

    return (
        <>
            <ContentLoader viewBox={`0 0 ${width} ${height}`} width="100%" height="100%" speed={2} {...props}>
                {list}
            </ContentLoader>
        </>
    );
};

export default Skeleton;
