import React from 'react';
import {withFocusable, Layout, FocusableProps} from '@noriginmedia/react-spatial-navigation';

import {connectToStore} from 'tools/connect';

import {StoreType} from 'stores/types';
import {ProductItemModel} from 'stores/product';

import Container from 'components/Grids/Container';
import FocusableProduct from 'components/Product';

import Bem from 'tools/bem';

import './style.scss';

const bem = Bem('ProductsList');

function scrollTo(node: HTMLElement, parent: HTMLElement): void {
    const {width: nodeWidth, left, right} = node.getBoundingClientRect();
  
    const {width: parentWidth} = parent.getBoundingClientRect();
  
    const {offsetLeft} = node;
    const itemsOnScreen = 4;

    if (left < 0) {
        parent.style.transform = `translateX(-${offsetLeft}px)`;
    } else if (right > window.innerWidth) {
        const gapWidth = (parentWidth - itemsOnScreen * nodeWidth) / itemsOnScreen;

        const nextItemIndex = Array.from(parent.children).indexOf(node);
        const leftItems = nextItemIndex - itemsOnScreen + 1;
        const leftScroll = leftItems * nodeWidth + gapWidth * leftItems;
  
        parent.style.transform = `translateX(-${Math.round(leftScroll)}px)`;
    }
}

interface ProductsListType {
    childKeyPrefix: string;
}


function isElementInViewport(el: Element): boolean {
    const rect = el.getBoundingClientRect();

    return (
        rect.top >= 0
        && rect.left >= 0
        && rect.bottom <= (window.innerHeight || document.documentElement.clientHeight)
        && rect.right <= (window.innerWidth || document.documentElement.clientWidth)
    );
}

const ProductsList: React.FC<StoreType & ProductsListType & FocusableProps> = ({products, ui, cart, setFocus}) => {
    const wrapRef = React.useRef<HTMLDivElement>(null);

    const onItemFocus = (id: string, layout: Layout): void => {
        if (wrapRef.current) {
            scrollTo(layout.node, wrapRef.current);

            setTimeout(() => {
                let visibleIndex = 1;
                let commonIndex = 0;

                if (wrapRef.current) {
                    for (const product of wrapRef.current.children) {
                        if (isElementInViewport(product)) {
                            products.setVisibility(products.items[commonIndex], true, visibleIndex);
                            visibleIndex += 1;
                        } else {
                            products.setVisibility(products.items[commonIndex], false, null);
                        }
                        commonIndex += 1;
                    }
                }
            }, 400);
        }
    };

    React.useEffect(() => {
        if (wrapRef.current && products.selectedId) {
            setFocus('CATALOG_ITEM_' + products.selectedId);
        }
    }, [products.selectedId]);

    return (
        <div className={bem()}>
            <Container>
                <div className={bem('row')} ref={wrapRef}>
                    {products.items.map((item: ProductItemModel) => {
                        return (
                            <FocusableProduct
                                index={item.index}
                                itemId={item.id}
                                countInCart={cart.itemsMap[item.id] && cart.itemsMap[item.id].length}
                                key={item.id}
                                focusKey={'CATALOG_ITEM_' + item.id}
                                onEnterPress={(): void => cart.addToCart(item.id)}
                                onBecameFocused={(layout: Layout): void => onItemFocus(item.id, layout)}
                                {...item}
                            />
                        );
                    })}
                </div>
            </Container>
        </div>
    );
};

const ConnectedList = connectToStore(ProductsList);

export default withFocusable({
    trackChildren: true,
})(ConnectedList);
