import React from 'react';
import {withFocusable, FocusableProps} from '@noriginmedia/react-spatial-navigation';
import {Link} from 'react-router-dom';

import {connectToStore} from 'tools/connect';

import {StoreType} from 'stores/types';

import Typography from 'components/Typography';

import withBem from 'hoc/withBem';
import {routes} from 'stores/ui';

import './style.scss';

const Cart: React.FC<StoreType & BemProps & FocusableProps> = ({cart, focused, bem}) => {
    return (
        <Link to={routes.CART}>
            <div className={bem.modify({focused})}>
                <div className={bem('icon')}>
                    {cart.count > 0 && (
                        <span className={bem('badge')}>
                            {cart.count}
                        </span>
                    )}
                </div>
                <div className={bem('block')}>
                    <div>
                        <Typography type="subheader">Корзина</Typography>
                    </div>
                    <div>
                        <Typography type="subheaderDescription">{cart.totalPrice} ₽</Typography>
                    </div>
                </div>
            </div>
        </Link>
    );
};

export default withFocusable()(withBem(connectToStore(Cart), 'Cart'));
